export default {
    name: 'NormalSwiper',
    props: {
        className: {
            default: 'normal',
            type: String
        },
        navigation: {
            default: true,
            type: Boolean
        },
        pagination: {
            default: true,
            type: Boolean
        }
    },
    data(){
        return{
            swiperOption: {
                slidesPerView: 'auto',
                centeredSlides: true,
                pagination: {
                    el: `#${this.className}-swiper  .swiper-pagination`,
                    dynamicBullets: false,
                    bulletActiveClass: 'index-bullet-Active',
                    bulletClass: 'index-bullet',
                },
                autoplay: {
                    disableOnInteraction: false,
                    delay: 5000
                },
                navigation: {
                    nextEl: `#${this.className}-swiper .swiper-button-next`,
                    prevEl: `#${this.className}-swiper .swiper-button-prev`
                },
                effect: 'fade'
            }
        }
    }
}
